.header {
  height: 87px;
  border-bottom: 1px solid #E4E4E4;
  padding: 0 !important;
}
.header .header-container {
  width: 100%;
  padding: 10px;
  background: white;
}
.header .header-container #basic-navbar-nav {
  background: white;
  width: 100%;
  justify-content: center !important;
}
.header .header-container #basic-navbar-nav .nav-link {
  color: #000000;
  padding: 0 50px;
  font-weight: 400;
  font-size: 16px;
}
@media (max-width: 991px) {
  .header .header-container #basic-navbar-nav .nav-link {
    padding: 10px 0;
  }
}

.footer {
  padding: 0 75px;
  margin-top: auto;
  width: 100%;
  font-size: 14px;
}
.footer .footer-container {
  border-top: 1px solid #E4E4E4;
  padding-top: 40px;
  padding-bottom: 60px;
  display: flex;
}
.footer .footer-container p {
  margin-bottom: 10px;
}
.footer .footer-container .mail-info {
  margin-bottom: 30px;
}
.footer .footer-container a {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  color: #373737;
}
.footer .footer-container .read-more {
  margin-left: 5px;
}

.home-page .home-info {
  padding-top: 47px;
  padding-bottom: 60px;
}
.home-page .home-info .info-content {
  margin-left: 30px;
  font-size: 18px;
  line-height: 27px;
}
.home-page .home-info .info-content .info-name {
  font-size: 40px;
  line-height: 60px;
}
.home-page .home-showcase {
  display: flex;
  justify-content: center;
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), #0C0C0C;
}
.home-page .home-showcase .img-text {
  font-size: 40px;
  color: #FFFFFF;
}
.home-page .home-showcase .img-smile {
  margin-left: 15px;
}
.home-page .home-showcase .img-ellipe {
  margin: 0 30px;
}
.home-page .home-info2 {
  padding-top: 30px;
  padding-bottom: 50px;
  font-size: 18px;
}
.home-page .home-info2 .info-lage {
  padding-top: 20px;
  font-size: 32px;
}
.home-page .home-info3 {
  height: 793px;
  background: #E1DDD6;
}
.home-page .home-info3 .text-small {
  font-size: 12px;
}
.home-page .home-info3 .text-normal {
  font-size: 62px;
}
.home-page .home-info3 .text-large {
  font-size: 120px;
}
.home-page .home-info3 .brands {
  display: flex;
  width: 100%;
  margin-left: 30px;
  height: 70px;
}
.home-page .home-info3 .brands .brands-line {
  margin-left: auto;
  width: 169px;
  border-bottom: 1px solid #000000;
}
.home-page .home-info3 .child1 {
  padding-top: 128px;
}
.home-page .home-info3 .child2 {
  padding-left: 15px !important;
  padding-top: 120px;
}
.home-page .home-info3 .child3 {
  display: flex;
  justify-content: end;
  padding-top: 245px;
}
.home-page .home-info3 .child3 .small-img {
  padding-top: 215px;
  display: flex;
  justify-content: end;
}

html,
body {
  height: 100%;
}

.container-fluid {
  padding-left: 0 !important;
  padding-right: 0 !important;
}

.row {
  width: 100%;
  margin: 0 !important;
}

.page-element {
  padding: 0 75px;
  font-size: 14px;
}

.bg-primary {
  background: #E1DDD6;
}

.page {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.img-container {
  position: relative;
  text-align: center;
  width: 100%;
}
.img-container .img-text {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

[class*=col-] {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
